const quastions = [
  {
    index: 1,
    question: "?",
    options: [
      { A: "\n" },
      { B: "\n" },
      { C: "\n\n" },
      { D: "\n" },
    ],
  },
  {
    index: 2,
    question: "?",
    options: [
      { A: "\n" },
      { B: "\n" },
      { C: "\n" },
      { D: "\n" },
    ],
  },
  {
    index: 3,
    question: "?",
    options: [
      { A: "\n" },
      { B: "\n" },
      { C: "\n\n" },
      { D: "\n" },
    ],
  },
  {
    index: 4,
    question: "?",
    options: [
      { A: "\n\n" },
      { B: "\n" },
      { C: "\n" },
      { D: "\n\n" },
    ],
  },
];

const App = () => {
  // 
  const [combinations, set_combinations] = useState([]); // 
  const [step, set_step] = useState(1); // 
  const [totalSteps, set_totalSteps] = useState(4); // 
  const [colorChosen, set_colorChosen] = useState(false); // 
  const [select, set_select] = useState(null); // 
  const [selections, set_selections] = useState([]); // 
  const [result, set_result] = useState(null); // Hex
  const [colorName, set_colorName] = useState(null); // Hex Name
  const [fadeIn, setFadeIn] = useState(false); // 
  const [isImgLoaded, setIsImgLoaded] = useState(false);
  const [isLoading, setIsLoading] = useState(false); //  Loading 
  // 
  const currentQuestion = quastions[step - 1];

  // 
  useEffect(() => {
    set_combinations(answers);
    setFadeIn(true); // 
  }, []);

  // 
  useEffect(() => {
    if (result?.color) {
      document.body.style.background = `#${result.color}`;
    } else {
      // 
      document.body.style.background = `url(${bg}) no-repeat center center / cover`;
      document.body.style.backgroundSize = "cover"; // 
      document.body.style.backgroundAttachment = "fixed"; // 
    }

    // 
    return () => {
      document.body.style.background = `url(${bg}) no-repeat center center / cover`;
    };
  }, [result?.color]);

  const handleSelect = (option) => {
    // 
    set_select(option); // 
    set_colorChosen(true); // ()
  };

  // 
  const handleNextStep = () => {
    const newSelections = [...selections, select]; // 
    set_selections(newSelections); // 
    set_select(null); // 

    // 
    if (newSelections.length === totalSteps) {
      setIsLoading(true); //  Loading 

      setTimeout(() => {
        const thirdAndFourthKey = newSelections.slice(3).join("");
        const matchAnswer = answers.find(
          (answer) => answer.combination === thirdAndFourthKey
        );

        set_result(matchAnswer || "Unique Combination!");
        set_colorName(
          matchAnswer?.color || ` (Key: ${thirdAndFourthKey})`
        );
        setIsLoading(false); //  Loading
      }, 2000); //  2 
      // const thirdAndFourthKey = newSelections.slice(3).join(""); // 

      // // 
      // const matchAnswer = answers.find(
      //   (answer) => answer.combination === thirdAndFourthKey
      // );
      // // 
      // set_result(matchAnswer || "Unique Combination!");
      // // 
      // set_colorName(
      //   matchAnswer?.color || ` (Key: ${thirdAndFourthKey})`
      // );
    } else {
      // 
      set_step((prev) => prev + 1);
      // 
      set_colorChosen(false);
    }
  };

  // 
  const handlePreviousStep = () => {
    if (step > 1) {
      const newSelections = selections.slice(0, -1); // 
      set_selections(newSelections); // 
      set_select(null); // 
      set_colorChosen(false); // 
      set_step((prevStep) => prevStep - 1); // 
    }
  };

  // 
  const resetTest = () => {
    set_step(1);
    set_selections([]);
    set_result(null);
    set_colorName(null);
    set_colorChosen(false);
  };

  // console.log("select", select);
  // console.log("result", result);
  // console.log("selections", selections);

  return (
    <div className={`test-page ${fadeIn ? "fade-in" : ""}`}>
      {isLoading ? (
        <Loading /> //  Loading 
      ) : (
        <div
          className="main-container"
          style={
            result
              ? {
                  backgroundColor: "rgba(255,255,255,1)",
                }
              : {
                  backgroundColor: "rgba(255,255,255,0.8)",
                }
          }
        >
          <div
            style={
              result
                ? {
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                    border: `1.5px solid #${result.color}`,
                    borderRadius: "36px",
                    width: "100%",
                    minHeight: "100vh",
                  }
                : null
            }
          >
            <div className="main-layout">
              {result ? (
                <img src={PIC} alt="coco" className="coco-img" />
              ) : (
                <div className="main-title">
                  <p className="titles" style={{ fontSize: "18px" }}>
                    GET YOUR
                  </p>
                  <img src={PIC} alt="coco" className="coco-img-main" />
                  {/* <p className="titles">ROUGE COCO</p> */}
                </div>
              )}
              <div className="progress">
                {!result ? (
                  <Progress step={step} totalSteps={totalSteps} />
                ) : // <Progress step={step} totalSteps={totalSteps} />
                null}
              </div>

              {result ? (
                <div className="final-layout">
                  <h1 className="final-h1">
                    
                    <span className="lipText">COCO</span>
                    
                  </h1>
                  <div className="final-card-area">
                    <div className="final-card">
                      <div className="card-up">
                        <img
                          src={result.bgL}
                          alt={result.colorsName}
                          className="card-img"
                        />

                        <div className="card-title">
                          <p
                            className="lipText"
                            style={{
                              fontSize: "20px",
                              fontWeight: "bold",
                            }}
                          >
                            {result.lipColorL}
                          </p>
                          <p
                            className="lipText"
                            style={{
                              fontSize: "14px",
                              fontWeight: "bold",
                            }}
                          >
                            {result.lipENameL}
                          </p>
                          <p
                            style={{
                              fontSize: "12px",
                              fontWeight: "bold",
                            }}
                          >
                            {result.lipCNameL}
                          </p>
                        </div>
                      </div>
                      <div className="card-lip">
                        {/*  */}
                        <img
                          src={result.bgL}
                          alt={result.colorsName}
                          className="lip-img-bg"
                        />

                        {/*  */}
                        <img
                          src={result.imgL}
                          alt={result.colorsName}
                          className="lip-img"
                          onLoad={() => setIsImgLoaded(true)} // 
                        />
                        {isImgLoaded && (
                          <span className="lip-img-shadow"></span>
                        )}
                      </div>
                    </div>
                    <div className="final-card">
                      <div className="card-up">
                        <img
                          src={result.bgR}
                          alt={result.colorsName}
                          className="card-img"
                        />

                        <div className="card-title">
                          <p
                            className="lipText"
                            style={{
                              fontSize: "20px",
                              fontWeight: "bold",
                            }}
                          >
                            {result.lipColorR}
                          </p>
                          <p
                            className="lipText"
                            style={{
                              fontSize: "14px",
                              fontWeight: "bold",
                            }}
                          >
                            {result.lipENameR}
                          </p>
                          <p
                            style={{
                              fontSize: "12px",
                              fontWeight: "bold",
                            }}
                          >
                            {result.lipCNameR}
                          </p>
                        </div>
                      </div>
                      <div className="card-lip">
                        {/*  */}
                        <img
                          src={result.bgR}
                          alt={result.colorsName}
                          className="lip-img-bg"
                        />

                        {/*  */}
                        <img
                          src={result.imgR}
                          alt={result.colorsName}
                          className="lip-img"
                        />
                        <span className="lip-img-shadow"></span>
                      </div>
                    </div>
                  </div>
                  <div className="lip-detail">
                    <p
                      className="lip-detail-title"
                      style={{
                        backgroundColor: `#${result.color}`,
                      }}
                    >
                      {result.colorsName}
                    </p>
                    <p className="lip-detail-p">
                      {result.text}
                      <span
                        style={{
                          color: `#${result.color}`,
                          fontWeight: "bold",
                        }}
                      >
                        {result.highlight}
                      </span>
                      {result.endText}
                    </p>
                  </div>

                  <button
                    type="primary"
                    onClick={resetTest}
                    className="reset"
                    style={{
                      backgroundColor: `#${result.color}`,
                    }}
                  >
                    
                  </button>
                </div>
              ) : (
                <div
                  style={{ maxWidth: 360, margin: "0 auto" }}
                  key={step}
                  className="animated-card"
                >
                  <div
                    style={{
                      fontSize: "15px",
                      fontWeight: "bold",
                      marginBottom: "40px",
                      color: "#424242",
                    }}
                  >
                    {currentQuestion.question}
                  </div>
                  <div
                    style={{
                      display: "grid",
                      gridTemplateColumns: "1fr 1fr", // 
                      gap: "10px", // 
                    }}
                  >
                    {currentQuestion.options.map((option, index) => {
                      const key = Object.keys(option)[0]; //  A, B, C, D  key
                      const value = option[key]; // 

                      return (
                        <Button
                          type="default"
                          key={key}
                          style={{
                            backgroundColor:
                              key === select ? "#d22d35" : "#ffffff",
                            color: key === select ? "#ffffff" : "#424242", // 
                            height: "120px", // 
                            maxWidth: "175px",
                            minWidth: "152px",
                            width: "100%",
                            borderRadius: "12px", // 
                            display: "flex",
                            alignItems: "center",
                            justifyContent: "center",
                            textAlign: "center",
                            whiteSpace: "pre-line", // 
                            lineHeight: "1.2",
                            padding: "0",
                            border: "none",
                          }}
                          onClick={() => handleSelect(key)} //  key (A, B, C, D)
                        >
                          <span
                            style={{
                              fontSize: "12px",
                              // fontWeight: "bold",
                              // textAlign: "center",
                            }}
                          >
                            {value}
                          </span>
                        </Button>
                      );
                    })}
                  </div>
                  <Space
                    style={{
                      display: "flex",
                      flexDirection: "row-reverse",
                      justifyContent: "space-between",
                      marginTop: "20px",
                    }}
                  >
                    <Button
                      type="primary"
                      onClick={handleNextStep}
                      disabled={!colorChosen} // 
                      style={{
                        backgroundColor: colorChosen ? "#d22d35" : "#ed9d8a",
                        color: "#ffffff",
                        padding: "0 2px",
                        borderRadius: "15px",
                        border: "none",
                      }}
                    >
                      <span
                        style={{
                          border: "1px solid #ffffff",
                          padding: "6px 18px",
                          borderRadius: "13px",
                        }}
                      >
                        
                      </span>
                    </Button>
                    {step > 1 && (
                      <Button
                        type="primary"
                        onClick={handlePreviousStep}
                        // disabled={!colorChosen} // 
                        style={{
                          backgroundColor: "#d22d35",
                          padding: "0 2px",
                          borderRadius: "15px",
                          border: "none",
                        }}
                      >
                        <span
                          style={{
                            border: "1px solid #ffffff",
                            padding: "6px 18px",
                            borderRadius: "13px",
                          }}
                        >
                          
                        </span>
                      </Button>
                    )}
                  </Space>
                </div>
              )}
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default App;
