import React, { useState } from "react";
import StartPage from "./StartPage/StartPage";
import TestPage from "./TestPage/TestPage"; // 
import MyHelmet from "./Helmet";
import "./index.css";

const App = () => {
  const [loading, setLoading] = useState(false); //  false loading
  const [showTest, setShowTest] = useState(false); // 
  const [fadeOut, setFadeOut] = useState(false); //  StartPage 

  //  StartPage 
  const handleStart = () => {
    setFadeOut(true); //  StartPage
    setTimeout(() => {
      setLoading(false); // Loading 
      setShowTest(true); // 
    }, 1000); //  1 
  };

  return (
    <div>
      <MyHelmet />
      {/* StartPage  */}
      <div className={`page ${fadeOut ? "fade-out" : ""}`}>
        {!loading && !showTest && <StartPage onStart={handleStart} />}
      </div>

      {/* TestPage  */}
      <div className={`page ${showTest ? "fade-in" : ""}`}>
        {!loading && showTest && <TestPage />}
      </div>
    </div>
  );
};

export default App;
